* {
  box-sizing: border-box;
}
html, body {
  margin: 0;
  padding: 0;
  font-family: 'Source Code Pro', monospace, sans-serif;
  font-size: 20px;
  color: #000;
}
@font-face {
  font-family: "SM Instagram";
  src: url('../fonts/SocialicoPlus-Plus.woff') format("woff");
}
@font-face {
  font-family: "SM FbTw";
  src: url('../fonts/Socialico-Regular.woff') format("woff");
}
//Background image with attachment -fixed
body {
  background-image: url("../images/physical-activity.jpeg");
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  position: relative;
  }
.background {
  position: absolute;
  background-color: rgba(0, 0, 0, .7);
  width: 100vw;
  height: 100vh;
}
.container {
  margin: 0 auto;
  width: 90%;
}
//NavBar with Buttons and LOGO
nav {
  display: flex;
  justify-content: space-between;
  width: 95%;
  margin: 0 auto;
  //LOGO image
  div {
    //Mobile Media query
    @media only screen and (max-width: 599px){
      height: 17vw;
      width: 18vw;
      margin-top: 1vh;
    }
    //Tablet Media query
    @media only screen and (min-width: 600px) and (max-width: 1024px){
      height: 15vw;
      width: 18vw;
      margin-top: 1.5vh;
    }
    //Desktop Media query
    @media only screen and (min-width: 1024px){
      height: 4vw;
      width: 4vw;
      margin-top: 1em;
    }
  }
  img{
    height: 100%;
    width: 100%;
  }
}
//Burger Menu Setting
.burgerMenu {
  display: inline-block;
  margin-left: 3.2em;
  cursor: pointer;
  .bar1, .bar2, .bar3 {
    width: 35px;
    height: 5px;
    background-color: #000;
    margin: 6px 0;
    transition: 0.3s;
  }
}
.burgerMenu:hover {
  .bar2 {
    background-color: #00bbff;
  }
}
.change {
  .bar1 {
    transform: rotate(-45deg) translate(-9px, 6px);
  }

  .bar2 {
    opacity: 0;
  }

  .bar3 {
    transform: rotate(45deg) translate(-8px, -8px);
  }
}
//General Section Settings
section {
  font-size: 6vw;
  text-align: center;
  margin: 10vh auto;
  min-height: 52vh;
  width: 90%;
  background-color: rgba(255, 255, 255, .9);
  box-shadow: 2px 2px 8px 2px #d3d3d3;
  padding: 1vh 1vw 1vh 1vw;
}
//First Section Settings - Visitors Page
#sectionOneVisitor{
  margin-top: 4vh;
  //Mobile Media query
  @media only screen and (max-width: 599px){
      h1 {
        font-size: 1.2em;
      }
      h2 {
        font-size: 1em;
      }
      p {
        font-size: 0.8em;
      }
    }
  //Tablet Media query
  @media only screen and (min-width: 600px) and (max-width: 1024px){
    h1 {
      font-size: 0.9em;
    }
    h2 {
      font-size: 0.7em;
    }
    p {
      font-size: 0.5em;
    }
  }
  //Desktop Media query
  @media only screen and (min-width: 1024px){
    h1 {
      font-size: 4vh;
    }
    h2 {
      font-size: 3.5vh;
    }
    p {
      font-size: 3vh;
    }
  }
}
//Second Section Settings - Visitors Page
#sectionTwoVisitor .container {
 div {
   font-size: 1.2em;
 }
  //Mobile Media query
  @media only screen and (max-width: 599px){
    div {
      font-size: 0.8em;
    }
  }
  //Tablet Media query
  @media only screen and (min-width: 600px) and (max-width: 1024px){
    div {
      font-size: 0.5em;
    }
  }
  //Desktop Media query
  @media only screen and (min-width: 1024px){
    div {
      font-size: 0.5em;
    }
  }
}
//Third Section Settings - Visitors Page
#sectionThirdVisitor .container {
  //Hover on SignUP Button
  button:hover {
    color: #fff;
    background-color: #000;
  }
  //Mobile Media query
  @media only screen and (max-width: 599px){
    h1 {
      font-size: 1.2em;
    }
    //Button styled
    button {
      padding: 0.4em;
      font-size: 0.7em;
