  //Tablet Media query
  @media only screen and (min-width: 600px) and (max-width: 1024px){
    h1 {
      font-size: 0.5em;
    }
    //Button styled
    button {
      padding: 0.2em;
      font-size: 0.5em;
      font-weight: 700;
      border-radius: 0.5em;
    }
  }
  //Desktop Media query
  @media only screen and (min-width: 1024px){
    h1 {
      font-size: 0.5em;
    }
    //Button styled
    button {
      padding: 0.2em;
      font-size: 0.3em;
      font-weight: 700;
      border-radius: 0.5em;
    }
  }
}
//Four Section Settings - Visitors Page
#sectionFourVisitor .container {
  button:hover {
    color: #fff;
    background-color: #000;
  }
  //Mobile Media query
  @media only screen and (max-width: 599px){
    h1{
        font-size: 1.2em;
    }
    //Button styled
    button {
      padding: 0.4em;
      font-size: 0.7em;
      font-weight: 700;
      border-radius: 0.5em;
    }
  }
  //Tablet Media query
  @media only screen and (min-width: 600px) and (max-width: 1024px){
    h1 {
      font-size: 0.5em;
    }
    //Button styled
    button {
      padding: 0.2em;
      font-size: 0.5em;
      font-weight: 700;
      border-radius: 0.5em;
    }
  }
  //Desktop Media query
  @media only screen and (min-width: 1024px){
